.side-panel{
    background-color: #0073B1;
    width: 0;
    padding: 0;
    height: 0;
    position: absolute;
    overflow-y: hidden;
    transition: width 150ms linear, padding 150ms linear;
    border-radius: 0 0 20px 0;
    left: 0;
    top: 50px;
    z-index: 1;
}

.side-panel li{
    margin: 10px 0;
}

.show-panel{
    width: 125px;
    height: auto;
    padding: 10px;
    z-index: 999;
}

.side-panel li:first-child{
    margin-top: 0;
}

.item{
    transition: background-color 0.2s linear, color 0.2s linear, background 0.2s linear;
    position: relative;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
}

.label{
    color: #ffffff;
    font-family: Arial, serif;
    font-size: 20px;
    line-height: 24px;
}

.arrow{
    position: absolute;
